<template>
    <div class="toollist">
        <div class="list">
            <van-col span="24" style="margin-bottom:0.2rem">
                <div class="topbar" @click.stop="change">
                    <div class="bar">
                        <van-col style="text-align:left" span="12">
                            <span>筛选</span>
                        </van-col>
                        <van-col span="12" style="font-size:0.28rem;text-align:right">
                            <span class="class">{{category}}</span>
                            <van-icon name="arrow-down"></van-icon>
                        </van-col>
                    </div>
                    <ul>
                        <li :class="index == active ? 'active' : ''" class="classList" @click.stop="select(index,list)" v-for="(list,index) in classList" :key="index">{{list}}</li>
                    </ul>
                </div>
            </van-col>
            <van-col span="24" style="padding-top:0.8rem">
                <van-list v-model="loading" :finished="finished" finished-text="到底啦~~" @load="onLoad">
                    <div class="tool" v-for="(list,index) in toollist" :key="index">
                        <div class="main">
                            <van-col span="8">
                                <div class="icon">
                                    <van-icon :name="list.icon"></van-icon>
                                </div>
                            </van-col>
                            <van-col span="16">
                                <p class="title">{{list.title}}</p>
                                <p class="desc">{{list.description}}</p>
                                <p class="btn">关 注</p>
                            </van-col>
                        </div>
                    </div>
                </van-list>
            </van-col>
        </div>
    </div>
</template>

<script>
var uid = Options.GetUserInfo().openid
export default {
    name:'toollist',
    data () {
        return {
            toollist:'',//工具列表
            category:'',//筛选内容
            classList:['全部','运动健身','英语','艺术','创业/职场','生活'],
            active:-1,
            loading:false,
            finished:false
        }
    },
    created(){
        this.tool(this)
    },
    onLoad(){
        this.tool(this,this.category)
    },
    methods:{
        tool(app,select = ''){
            /**
             * @params select 筛选主题
             */
            if(select == ''){
                var data = {uid:uid,catalog:false}
            }else{
                var data = {uid:uid,catalog:false,select:select}
            }
            TD_Request('op','opt',data,function(code,data){
                console.log(data)
                if(data.accounts.data.length == 0 || data.accounts.count == app.toollist.length){
                    app.loading = true;
                    app.finished = true;
                }
                app.toollist = data.accounts.data;
                app.classList = data.typelist;
                app.category = data.select;
            },function(code,data){
                console.log(data)
            })
        },
        change(){
            $('ul').slideToggle();
        },
        select(index,item){
            console.log(index,item)
            this.active = index;
            this.category = item;
            $('ul').slideUp();
        }
    }
}
</script>

<style lang="less" scoped>
    .toollist{
        width: 100%;
        height:100%;
        .list{
            width: 6.9rem;
            margin: 0.2rem auto 0;
            .topbar{
                background: #fff;
                height: auto;
                border-radius: 0.1rem;
                line-height: 0.86rem;
                position: absolute;
                width: 6.9rem;
                .bar{
                    width: 6.3rem;
                    margin: 0 auto;
                    overflow: hidden;
                    span{
                        font-size: 0.28rem;
                        color: #999;
                    }
                    .class{
                        color: #00d094;
                    }
                }
                ul{
                    display: none;
                }
                .classList{
                    width: 100%;
                    border-top:1px solid #eee;
                    height: 0.85rem;
                    line-height: 0.85rem;
                    text-align: left;
                    width: 6.3rem;
                    margin: 0 auto;
                    font-size: 0.28rem;
                }
                .classList.active{
                    color: #00d094;
                }
            }
            .tool{
                width: 100%;
                height: 2.44rem;
                background: url(https://tdream.antit.top/xdgj_bj.png) no-repeat center center / 6.9rem 2.44rem;
                .main{
                    width: 6.3rem;
                    margin: 0 auto;
                    padding-top: 0.3rem;
                    .icon{
                        font-size: 1.6rem;
                    }
                    .title{
                        font-size: 0.28rem;
                        margin-bottom: 0.2rem;
                    }
                    .desc{
                        font-size: 0.26rem;
                        color: #999;
                    }
                    .btn{
                        float: right;
                        width: 1.4rem;
                        height: 0.45rem;
                        border-radius: 0.225rem;
                        background: #00d094;
                        color: #fff;
                        text-align: center;
                    }
                }
            }
        }
    }
</style>



