<template>
    <div class="tool">
        <tool-list v-if="tool_list"/>
    </div>
</template>

<script>
var uid = Options.GetUserInfo().openid;
import toolList from '@/components/toollist'
export default {
    name:'tool',
    data () {
        return {
            tool_list:true,
            tool_category:true,
        }
    },
    components:{
        toolList,
    },
    created(){
        this.eomp(this)
    },
    methods:{
        eomp(app){
            TD_Request('op','eomp',{uid:uid},function(code,data){
                console.log(data)
                app.tool_list = false
            },function(code,data){
                if(code == 82){
                    app.tool_category = false;
                }
            })
        }
    }
}
</script>

<style lang="less" scoped>

</style>



